import React, { useState } from 'react';
import Header from './Header';
import { useNavigate } from 'react-router-dom';

const SignUp = () => {
    const navigate = useNavigate();
    const [data, setData] = useState({
        name: "",
        email: "",
        password: ""
    });
    const handle = (e) => {
        const { name, value } = e.target;
        setData({ ...data, [name]: value })
    };

    const submit = async () => {
        try {
            const res = await fetch("/", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(data)
            });
            const val = await res.json();
            if (res.status === 200) {
                navigate('/login');
            } else {
                alert(val.msg);
            }
        } catch (err) {
            console.log('err', err);
        }
    }


    return (
        <div className='h-screen w-screen bg-gray-200 grid place-items-center justify-items-center overflow-auto scrollbar-hide'>
            <div className='grid grid-cols-1 gap-3'>
                <Header />
                <div>
                    <h2 className='text-red-700'>name*</h2>
                    <input className='p-2 text-lg bg-slate-200 rounded outline-none border border-blue-600 w-[300px]' type="text" name="name" value={data.name} onChange={handle} />
                </div>
                <div>
                    <h2 className='text-red-700'>Email*</h2>
                    <input className='p-2 text-lg bg-slate-200 rounded outline-none border border-blue-600 w-[300px]' type="email" name="email" value={data.email} onChange={handle} />
                </div>
                <div>
                    <h2 className='text-red-700'>password*</h2>
                    <input className='p-2 text-lg bg-slate-200 rounded outline-none border border-blue-600 w-[300px]' type="password" name="password" value={data.password} onChange={handle} />
                </div>
                <button onClick={submit} className='text-lg bg-emerald-600 p-1.5 rounded-lg'>submit</button>
            </div>
        </div>
    )
}

export default SignUp
