import { useDispatch } from 'react-redux';
import { VALUE } from "./Action/index";
import React, { useRef, useState } from 'react';
import Webcam from "react-webcam";
import MicIcon from '@material-ui/icons/Mic';
import MicOffIcon from '@material-ui/icons/MicOff';
import AddIcCallIcon from '@material-ui/icons/AddIcCall';
import { useNavigate } from 'react-router-dom';
import io from "socket.io-client"
import { useEffect } from 'react';
const socket = io.connect('/');



const Join = ({ audio, setAudio }) => {
    const dispatch = useDispatch();
    const [inputData, setInputData] = useState('');
    const [create, setCreate] = useState('');
    const [UserData, setUserData] = useState({});
    const navigate = useNavigate();
    const myStream = useRef();

    const auth = async () => {
        try {
            const res = await fetch("/hidden", {
                method: "GET",
                headers: {
                    "Content-Type": "application/json"
                },
            });
            if (res.status === 400) {
                navigate('/login');
                throw new Error();
            }
            const val = await res.json();
            socket.emit('join', val._id);
            setUserData({ ...val });
        } catch (err) {
            console.log(err);
        }
    }

    useEffect(() => {
        auth();
    }, []);

    const join = async () => {
        try {
            if (inputData) {
                const { _id } = UserData;
                const res = await fetch(`/join/${_id}/${inputData}`, {
                    method: "GET",
                    headers: {
                        "Content-Type": "application/json"
                    },
                });

                const val = await res.json();
                if (res.status === 200) {
                    navigate(`/room/${inputData}`);

                } else if (res.status === 201) {
                    socket.emit('permit', val.Admin._id, _id, UserData.name);
                    socket.on('reply', (bool) => {
                        if (bool) {
                            dispatch(VALUE(true));
                            navigate(`/room/${inputData}`);
                        }
                    })
                }
            }
        } catch (err) {
            console.log(err);
        }
    }

    const createMetting = async (e) => {
        try {
            if (create) {
                const { _id } = UserData;
                const res = await fetch("/metting", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify({ _id, create })
                });
                const val = await res.json();

                if (res.status === 201) {
                    setInputData(val.msg);
                    setCreate('');
                    alert(val.msg);
                } else if (res.status === 202) {
                    alert(val.msg);
                    setCreate('');
                } else {
                    setInputData(create);
                    setCreate('');
                }
            }
        } catch (err) {
            console.log(err);
        }
    }


    return (
        <div className="w-screen h-screen bg-cyan-800 overflow-auto scrollbar-hide">
            <h2 className='text-xl md:text-3xl text-center my-4 text-slate-900'>Welcome to Video chatting app</h2>
            <div className='grid place-items-center justify-items-center m-10'>
                <div>
                    <input type="text" value={create} onChange={(e) => setCreate(e.target.value)} className='text-lg p-2  outline-none rounded-l-lg mt-3 bg-slate-500' placeholder='Enter any Id' />
                    <button onClick={createMetting} className='bg-sky-500 p-2.5 rounded-r-lg mb-5'>create</button>
                </div>
                <div className='rounded-2xl bg-teal-700 shadow-xl'>
                    <Webcam height={350} width={350} ref={myStream} className='rounded-md' />
                    <div className='flex justify-evenly my-1.5 sm:my-1.5 md:my-2.5 lg:my-4'>
                        {
                            audio ? <MicIcon className='text-gray-800 cursor-pointer' onClick={() => setAudio(!audio)} style={{ fontSize: "2rem" }} /> :
                                <MicOffIcon className='text-gray-900 cursor-pointer' onClick={() => setAudio(!audio)} style={{ fontSize: "2rem" }} />
                        }
                        <AddIcCallIcon className='text-gray-900 cursor-pointer' onClick={join} style={{ fontSize: "2rem" }} />
                    </div>
                </div>
                <input type="text" value={inputData} onChange={(e) => setInputData(e.target.value)} className='text-lg p-2 md:px-4  outline-none rounded-lg mt-5 bg-slate-500' />
            </div>
        </div>
    )
}

export default Join;
export { socket };

