import React, { useRef, useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { socket } from "./Join"
import { useSelector } from "react-redux";
import Peer from "simple-peer";
import Web from "./Web";

const App = ({ audio, setAudio }) => {
    const state = useSelector((state) => state.ReducerState);
    const navigate = useNavigate();
    const { roomID } = useParams();
    const peersRef = useRef([]);
    const userVideo = useRef();
    const [peers, setPeers] = useState([]);
    const peersVar = useRef([]);

    const auth = async () => {
        try {
            const res = await fetch(`/check/${roomID}/${state}`, {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                },
                credentials: "include"
            });
            if (res.status === 400) {
                navigate('/login');
                throw new Error();
            } else if (res.status === 401) {
                navigate('/join');
                throw new Error();
            } else if (res.status === 200 || res.status === 201) {
                const val = await res.json();
                socket.emit("join", val._id);
            }
        } catch (err) {
            console.log(err);
        }
    }


    useEffect(() => {
        auth();
        socket.on("pending", (id, name) => {
            let ans = window.confirm(name);
            socket.emit("ans", id, ans);
        });

        navigator.mediaDevices.getUserMedia({ video: true, audio: true }).then(stream => {
            userVideo.current.srcObject = stream;
            socket.emit("join room", roomID);
            socket.on("all users", users => {
                const peers = [];
                users.forEach(userID => {
                    const peer = createPeer(userID, socket.id, stream);
                    peersRef.current.push({
                        peerID: userID,
                        peer,
                    })
                    peers.push(peer);

                })
                setPeers(peers);
                peersVar.current.push(...peers);
            })

            socket.on("user joined", payload => {
                const peer = addPeer(payload.signal, payload.callerID, stream);
                peersRef.current.push({
                    peerID: payload.callerID,
                    peer,
                })

                setPeers(users => [...users, peer]);
                peersVar.current.push(peer);
            });

            socket.on("receiving returned signal", payload => {
                const item = peersRef.current.find(p => p.peerID === payload.id);
                item.peer.signal(payload.signal);
            });

            socket.on('dis-connect', (ID) => {
                const item = peersRef.current.find(p => p.peerID === ID);
                peersVar.current.splice(peersVar.current.indexOf(item.peer), 1);
                setPeers(peersVar.current);
            })
        })
    }, []);

    function createPeer(userToSignal, callerID, stream) {
        const peer = new Peer({
            initiator: true,
            trickle: false,
            stream,
        });

        peer.on("signal", signal => {
            socket.emit("sending signal", { userToSignal, callerID, signal })
        })

        return peer;
    }

    function addPeer(incomingSignal, callerID, stream) {
        const peer = new Peer({
            initiator: false,
            trickle: false,
            stream,
        })

        peer.on("signal", signal => {
            socket.emit("returning signal", { signal, callerID })
        })
        peer.signal(incomingSignal);
        return peer;
    }
    return (
        <div>
            <video playsInline ref={userVideo} autoPlay style={{ width: "300px" }} />
            {
                peers.map((peer, index) => {
                    return (
                        <Web key={index} peer={peer} />
                    )
                })
            }
        </div>
    )
}

export default App;


