import React, { useRef, useEffect } from 'react';



const Web = (props) => {
  const ref = useRef();
  useEffect(() => {
    props.peer.on("stream", stream => {
      ref.current.srcObject = stream;
    })
  },[])
  return (
    <div >
      <video playsInline ref={ref} autoPlay className='w-[330px] rounded' />
    </div>
  )
}

export default Web



