import React from 'react';
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import PersonIcon from '@material-ui/icons/Person';
import { NavLink } from "react-router-dom";

const Header = () => {
    return (
        <div className='flex justify-around my-2'>
            <NavLink to='/' className={({ isActive }) => isActive ? "text-green-600" : null}>
                <div className='flex flex-col cursor-pointer'>
                    <PersonIcon className="fa fa-user text-lg mx-auto" aria-hidden="true" />
                    <h2 className='text-xl'>signup</h2>
                </div>
            </NavLink>
            <NavLink to='/login' className={({ isActive }) => isActive ? "text-green-600" : null}>
                <div className='flex flex-col cursor-pointer'>
                    <VpnKeyIcon className="fa fa-user text-lg mx-auto" aria-hidden="true" />
                    <h2 className='text-xl'>login</h2>
                </div>
            </NavLink>
        </div>
    )
}

export default Header;
